.body-question {
  background-color: $bg-color-body;
  min-height: 199vh;
}

.main-question {
  width: 100%;
  height: 100%;
  position: absolute;
  // z-index: 999;
  top: 0;
  background-color: #e0e7ff;
  animation: tranformS 0.5s ease-in;
  transform-origin: left;
  // will-change: opacity, transform;
}
@keyframes tranformS {
  from {
    opacity: 0;
    transform: scale(0);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.clock-goiy {
  position: absolute;
  bottom: -19px;
  z-index: 1;
  right: 20px;
}

.artifacts {
  // background: $bg-color-primary;
  background-color: $bg-color-box-primary;
  height: 80px;
  border-radius: $radius-box;
  position: relative;
  padding: 10px 10px;
  &::before {
    content: "";
    background-color: rgb(223, 223, 223);
    height: 2px;
    width: 100%;
    position: absolute;
    top: 50%;
  }
}

.mySlides-artifacts {
  @include square(60px);
  height: 60px;
  border-radius: $radius-box;
  background-color: #e0e7ff;
  margin: 0 2rem;
  position: relative;
  z-index: 1;
  @include box-shadow;
  .mySlides-artifacts--item {
    --length: 100%;
    width: var(--length);
    height: var(--length);
    position: absolute;
    z-index: 1;
    background-image: url(../../assets/image/BaoVat.png);
    background-repeat: no-repeat;
    top: 0px;
    left: 25%;
  }
  .icon-choice {
    background-image: url(../../assets/image/icon-choice.png);
    top: -25%;
    left: 20%;
    @extend .mySlides-artifacts--item;
  }
  img {
    height: 80%;
    /* width: 50%; */
    object-fit: contain;
    /* margin: 10% 0 0 25%; */
    position: absolute;
  }
}

.container-decription-history {
  margin-top: 10px;
  /* margin: 20px auto auto auto !important; */
  background: $bg-color-box-primary;
  height: 245px;
  text-align: center;
  border-radius: 10px;
  p {
    padding: 0;
  }
}

.container-suggestions {
  margin: 10px auto auto auto !important;
  background: $bg-color-box-primary;
  height: 126px;
  text-align: center;
  border-radius: 10px;
  p {
    margin-bottom: 0;
    height: 30%;
    margin-top: 17%;
  }
}
