.dice {
  position: relative;
  width: 100px;
  height: 100px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}

.dot {
  position: absolute;
  width: 20px;
  height: 20px;
  margin: -10px 5px 5px -10px;
  border-radius: 20px;
  background-color: #f25f5c;
  box-shadow: inset 2px 2px #d90429;
}

.dice-one {
  position: absolute;
  left: 150px;
}

.dice-two {
  position: absolute;
  left: -50px;
}

.side {
  position: absolute;
  background-color: #fff;
  border-radius: 5px;
  width: 100px;
  height: 100px;
  border: 1px solid #e5e5e5;
  text-align: center;
  line-height: 2em;
}

.side:nth-child(1) {
  -webkit-transform: translateZ(3.1em);
          transform: translateZ(3.1em);
}

.side:nth-child(6) {
  -webkit-transform: rotateY(90deg) translateZ(3.1em);
          transform: rotateY(90deg) translateZ(3.1em);
}

.side:nth-child(3) {
  -webkit-transform: rotateY(-90deg) translateZ(3.1em);
          transform: rotateY(-90deg) translateZ(3.1em);
}

.side:nth-child(4) {
  -webkit-transform: rotateX(90deg) translateZ(3.1em);
          transform: rotateX(90deg) translateZ(3.1em);
}

.side:nth-child(5) {
  -webkit-transform: rotateX(-90deg) translateZ(3.1em);
          transform: rotateX(-90deg) translateZ(3.1em);
}

.side:nth-child(2) {
  -webkit-transform: rotateY(-180deg) translateZ(3.1em);
          transform: rotateY(-180deg) translateZ(3.1em);
}

.show-1 {
  -webkit-transform: rotateX(720deg) rotateZ(-720deg);
          transform: rotateX(720deg) rotateZ(-720deg);
